import './Skills.css'
import { useSelector } from 'react-redux'
import { motion } from 'framer-motion'

const Skills = () => {
    const { data } = useSelector((state) => state.data)

    //scroll
    let ourskill = document.querySelector(".prog-holder");
    window.onscroll = function () {
    let skillsoffsettop = ourskill.offsetTop;
    // this.console.log(skillsoffsettop);
    let skillouterheight = ourskill.offsetHeight;
    //    this.console.log(skillouterheight);
    let windowheight = this.innerHeight;
    // console.log(windowheight);
    let windowscrolltop = this.pageYOffset;
    if (windowscrolltop > (skillsoffsettop + skillouterheight - windowheight)) {

        let allskill = document.querySelectorAll(".prog-holder .prog span");
        allskill.forEach(skill => {
            skill.style.width = skill.dataset.progress;
        });
    }
}
    return (
        <>
            {
                data.map((it, idU) => (
                    <div className="prog" key={idU}>
                        <span
                        data-progress ={`${it.skill_percent}% `}
                            style={{ width: 0 , transition: 'all 1.1s'} }>{it.skill_name}</span>
                        <hr style={{ width: `calc(100% - ${it.skill_percent}%)` }} />
                        <p> {it.skill_percent}%</p>
                    </div>
                ))
            }
        </>
    )
}
export default Skills


