import './about.css'
import Skills from './skiils/Skills'
import Spanph from './SpanPH/Spanph'

import { useEffect } from 'react'
// data
import { useDispatch } from 'react-redux'
import { getData } from '../../store/mSlice'
import { useSelector } from 'react-redux'
import loading from '../../images/loading_holder.jpg'
import { LazyLoadImage } from 'react-lazy-load-image-component'
import 'react-lazy-load-image-component/src/effects/blur.css';

// import Lottie from "lottie-react";
// import groovyWalkAnimation from "../Loading_1.json";

const About = () => {
  // const { isLoading } = useSelector((state) => state.data)

  const { services , isLoading } = useSelector((state) => state.data)
  const dispatch = useDispatch();
  console.log(services)
  useEffect(() => {
    dispatch(getData({ id: 1 }));
  }, [dispatch])

  const topAbout = [
    {
      span: '2020 - 2021',
      p: "I started BackEnd Development by learning PHP Pure and Mysql and created API to be used in mobile app development",
    },
    {
      span: services.profile_year,
      p: "I started learning the Flutter framework  and Dart programming language and combined my experience with filters and PHP to create a range of applications for customers",
    },
  ]
  return (
    <div className='about' id='about'>
      <div className="top">
        {
          topAbout.map((item) => (
            <Spanph key={item.span} span={item.span} p={item.p} />
          ))
        }
      </div>
      <div className="buttom">
        <div className="left">

          {/* {
            isLoading ? (<img src={loading} alt=''/>) : (
            <img src={`https://jostore2000.000webhostapp.com/upload/projects/${services.profile_image} `} alt='' />
            )
          } */}
          <LazyLoadImage
            className='image' 
            effect="blur"
            src={`https://jostore2000.000webhostapp.com/upload/projects/${services.profile_image} `} alt=''
            />
        </div>
        <div className="right">
          <h2>SKILLS</h2>
          <p>This is all the skills listed below 
          </p>
          <div className="prog-holder">
            <Skills />
          </div>
        </div>
      </div>
    </div>
  )
}

export default About